
import { defineComponent, reactive, ref } from 'vue';
import Toggle from '@vueform/toggle';
import { QuickFetch, QuickData } from '@/quick';
import { QuillEditor } from '@vueup/vue-quill';
import '@vueup/vue-quill/dist/vue-quill.snow.css';

export default defineComponent({
  setup(props, context) {
    // quick
    const qf = new QuickFetch();
    const qd = new QuickData();
    // data
    const pinned = ref(0);
    const status = ref(1);
    const date = ref('');
    const title = ref('');
    const category = ref(0);
    const content: any = ref('');
    const links: any = reactive([]);
    // submit
    function submitAll() {
      qf.Dataset()
        .AddObj('date', date)
        .AddObj('category', category)
        .AddObj('title', title)
        .AddObj('content', content)
        .AddObj('pinned', pinned)
        .AddObj('release_status', status)
        .Add('links', JSON.stringify(links));
      if (new Blob([JSON.stringify(qf.GetDset())]).size > 10000000) {
        qd.Alert('總大小大於10Mb，無法上傳');
        return;
      }
      qf.Url('bulletin/add').Post(qf.GetDset()).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已新增公告');
          context.emit('refresh');
          context.emit('close_modal');
        }
      });
    }
    return {
      pinned,
      status,
      date,
      title,
      category,
      content,
      links,
      submitAll,
    };
  },
  components: {
    Toggle,
    QuillEditor,
  },
});
