
import {
  computed,
  defineComponent, reactive, ref, watch,
} from 'vue';
import Toggle from '@vueform/toggle';
import { QuickFetch, QuickData } from '@/quick';
import { useStore } from 'vuex';
import { QuillEditor } from '@vueup/vue-quill';
import '@vueup/vue-quill/dist/vue-quill.snow.css';

export default defineComponent({
  props: ['postData'],
  setup(props, context) {
    // quick
    const qf = new QuickFetch();
    const qd = new QuickData();
    // readonly
    const isReadonly = ref(true);
    // store
    const store = useStore();
    // data
    const data = reactive(props.postData);
    const pinned = ref(data.pinned);
    const status = ref(data.release_status);
    const date = ref(data.date);
    const title = ref(data.title);
    const category = ref(data.category);
    const content: any = ref(data.content);
    const links: any = reactive(JSON.parse(data.links));
    // submit
    function submitAll() {
      qf.Dataset()
        .AddObj('date', date)
        .AddObj('category', category)
        .AddObj('title', title)
        .AddObj('content', content)
        .AddObj('pinned', pinned)
        .AddObj('release_status', status)
        .Add('links', JSON.stringify(links));
      if (new Blob([JSON.stringify(qf.GetDset())]).size > 10000000) {
        qd.Alert('總大小大於10Mb，無法上傳');
        return;
      }
      qf.Url('bulletin/edit').Patch(data.bulletin_id, qf.GetDset()).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已更新公告');
          context.emit('close_modal');
          context.emit('refresh');
        }
      });
    }
    function deletePost() {
      qd.Confirm('確定要刪除嗎？');
    }
    const confirmAnswer = computed(() => store.state.confirmAnswer);
    watch(confirmAnswer, (val: any) => {
      if (val === 'y') {
        console.log(data);
        qf.Url('bulletin/delete').Delete(data.bulletin_id).then((res: any) => {
          if (res.message === 'done') {
            qd.Alert('已刪除公告');
            context.emit('close_modal');
            context.emit('refresh');
          }
        });
      }
      if (val === 'n') {
        qd.Alert('取消刪除');
      }
      qd.ResetConfirm();
    });
    return {
      pinned,
      status,
      date,
      title,
      category,
      content,
      links,
      submitAll,
      isReadonly,
      deletePost,
    };
  },
  components: {
    Toggle,
    QuillEditor,
  },
});
