
import { defineComponent, ref } from 'vue';
import FullModal from '@/components/FullModal.vue';
import AddPost from '@/components/bulletin/AddPost.vue';
import EditPost from '@/components/bulletin/EditPost.vue';
import { QuickData, QuickFetch } from '@/quick';

export default defineComponent({
  setup() {
    const displayModal = ref(0);
    const qf = new QuickFetch();
    const qd = new QuickData();
    const postList: any = ref([]);
    const selectedPost: any = ref({});
    function getPost() {
      qf.Url('bulletin/list').GetAll(postList);
    }
    getPost();
    return {
      displayModal,
      postList,
      selectedPost,
      openPost: (input: any) => {
        selectedPost.value = input;
        displayModal.value = 2;
      },
      getPost,
    };
  },
  components: {
    FullModal,
    AddPost,
    EditPost,
  },
});
